@import "../../../styles/base";
@import "../../../styles/animation.scss";
.desk {
	position: absolute;
	width: 100vw;
	z-index: -1;
}
.experience {
	height: 50vh;
	top: -20px;
	position: relative;
	&_title {
		position: absolute;
		top: -100px;
		.dots_white {
			width: 250px;
			position: absolute;
			left: 0;
		}

		&_text {
			padding-top: 100px;
			padding-left: 5rem;
			font-size: 2.5rem;
			font-weight: 400;
			font-family: "Caveat";
			color: $font-gray;
			animation: fadeIn 2s;
			z-index: 2;
		}
	}
}
